/* src/App.css */

body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: #f9f9f9;
}

.app-container {
  text-align: center;
}

.page-content {
  padding: 20px;
}

.search-bar {
  width: 80%;
  max-width: 500px;
  padding: 12px 20px;
  margin: 20px auto;
  border-radius: 25px;
  border: 2px solid #ccc;
  font-size: 16px;
  display: block;
  transition: all 0.3s ease;
}

.search-bar:focus {
  outline: none;
  border-color: #4caf50;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.4);
}

.animals-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 20px;
}

.about-page {
  max-width: 700px;
  margin: auto;
  text-align: left;
  background: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
}
